import '../styles/picture-list.scss'
import { message, Icon } from 'antd'
import PropTypes from 'prop-types'
import copy from '../utils/copy'
import ReactDOM from 'react-dom'
import React from 'react'

export default class PictureList extends React.Component {
  static propTypes = {
    pictures: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
    grid: PropTypes.bool
  }

  removePictureByKey = key => {
    this.props.onRemovePicture(key)
  }

  render () {
    const { props: { pictures, grid, children }, removePictureByKey } = this
    return (
      <section className={`picture-list ${grid ? 'grid' : ''}`}>
        {pictures.map((url, key) => {
          return (
            <div key={key}>
              <Icon onClick={removePictureByKey.bind(this, key)} type="delete" />
              <img onClick={copy.bind(this, url)} src={url} alt="" />
              {children && children(url) }
            </div>
          )
        })}
      </section>
    )
  }
}



