import PictureList from '../components/PictureList'
import VovDragger from '../components/VovDragger'
import { observable, action } from 'mobx'
import castArray from 'lodash/castArray'
import { observer } from 'mobx-react'
import Snag from '../components/Snag'
import ReactDOM from 'react-dom'
import '../styles/vov.scss'
import React from 'react'

@observer
export default class Vov extends React.Component {
  @observable pictures = []

  @action.bound
  setPictures (pictures) {
    this.pictures = castArray(pictures)
  }

  render () {
    return (
      <section className="vov">
        {this.pictures.length
            ? <PictureList pictures={this.pictures} />
            : <VovDragger></VovDragger>}
        <Snag setPictures={this.setPictures}></Snag>
      </section>
    )
  }
}



