import axios from 'axios'
import copy from './copy'

export default async (file) => {
  const formData = new FormData()
  formData.append('smfile', file)
  const { data: { data: { url } } } = await axios.post('https://sm.ms/api/upload', formData)
  copy(url)
  return url
}



