import PictureList from '../components/PictureList'
import storage from '../utils/storage'
import PropTypes from 'prop-types'
import ReactDOM from 'react-dom'
import '../styles/records.scss'
import React from 'react'

storage.set('pictures', [
  'https://images.unsplash.com/44/Y51aFguqRcGTgsYRYBXV_20140104_085932.jpg?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1920&fit=crop&ixid=eyJhcHBfaWQiOjczMTN9&s=9c45ff49cb05422328ff7b42c8fff309',
  'https://images.unsplash.com/photo-1437315306147-0923bdb3fc12?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1920&fit=crop&ixid=eyJhcHBfaWQiOjczMTN9&s=8b62ebc4125e69cc67828df370a911af',
  'https://images.unsplash.com/photo-1516714819001-8ee7a13b71d7?ixlib=rb-0.3.5&q=80&fm=jpg&crop=entropy&cs=tinysrgb&w=1920&fit=crop&ixid=eyJhcHBfaWQiOjczMTN9&s=40a00f9ddc20388d4007058206f0e5fc'
])

export default class Records extends React.Component {
  state = {
    pictures: storage.get('pictures', [])
  }

  onRemovePicture = key => {
    const pictures = this.state.pictures.filter((v, k) => k !== key)
    this.setState({ pictures })
    storage.set('pictures', pictures)
  }

  render () {
    const { onRemovePicture, state: { pictures } } = this

    return (
      <section className="records">
        {pictures.length
          ? <PictureList
              onRemovePicture={onRemovePicture}
              pictures={pictures}
              grid>
            </PictureList>
          : <p className="empty">Emmmm, Here is the barrens.</p>}
      </section>
    )
  }
}



